.tariffs {
  position: relative;
  background-color: #f2f2f2;
  padding-top: 58px;
  
  &::before {
    position: absolute;
    content: "";
    width: 0;
    right: 0;
    top: -35px;
    border: 35px solid transparent;
    border-right: 100vw solid rgb(255, 68, 0);
    border-bottom: 0px solid transparent;
  }
}

.tariffs__wrapper {
  overflow: hidden;
}

.tariffs__table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 840px;
  margin-left: calc(-280px + (100vw - 280px) / 2);
  margin-bottom: 18px;
}

.tariffs__table tbody {
  display: flex;
  justify-content: space-between;
}

.tariffs__list {
  width: 280px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #ffffff;
}

.tariffs__name {
  display: block;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: #283645;
  font-family: "Opensansbold";
  color: #ffffff;
  font-size: 18px;
  line-height: 30px;
  text-align: center;
  text-transform: uppercase;  
}

.tariffs__name--hit {
  position: relative;
  background-image: url("../img/bg-hit.svg");
  background-repeat: no-repeat;
  border-left: 2px solid #ffffff;
  border-right: 2px solid #ffffff;
  
  &::before {
    content: "";
    position: absolute;
    top: 8px;
    left: 8px;
    font-size: 14px;
    letter-spacing: 0.2em;
    transform: rotate(-45deg);
  }
}

.tariffs__price {
  font-family: "Opensanslight";
  display: table;
  margin: 0 auto;
  font-size: 24px;
}

.tariffs__value {
  display: block;
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
  height: 28px;
  padding-left: 20px;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 30px;
  border-bottom: 2px solid #e5e5e5;
  border-left: 2px solid #e5e5e5;
}

.tariffs__value--yes,
.tariffs__value--no {
  position: relative;
}

.tariffs__value--no::before {
  position: absolute;
  content: "";
  top: 20px;
  right: 20px;
  background-image: url("../img/icon-no.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 26px;
  height: 27px;
}

.tariffs__value--yes::before {
  position: absolute;
  content: "";
  top: 20px;
  right: 20px;
  background-image: url("../img/icon-yes.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 31px;
  height: 21px;
}

@media (min-width: $tablet-width) {
  .tariffs {
    padding-top: 50px;
    padding-bottom: 65px;
  }  
  
  .tariffs__table {
    width: 620px;    
    margin-left: 0;
    margin: 0 auto;
  }

  .tariffs__table tbody {
    justify-content: flex-end;    
  }

  .tariffs__list {
    position:relative;
    width: 155px;
    margin: 0;     
    
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 2px;
      height: 100%;
      background-color: #e5e5e5;
      z-index: 2;
    }
    
    &:first-child .tariffs__value {
      margin-left: -155px;
    }

    &:nth-child(2) .tariffs__value,
    &:last-child .tariffs__value {
      font-size: 0;
    }

    &:first-child .tariffs__value--no::before {
      margin-right: -95px;
    }

    &:first-child .tariffs__value--yes::before {
      margin-right: -95px;
    }
  }

  .tariffs__name {
    padding-top: 12px;
    padding-bottom: 8px;
  }
  
  .tariffs__name--hit {
    background-size: 60px auto;
    
    &::before {
      top: 7px; 
      letter-spacing: 0;      
      font-family: "Opensanslight";
    }
  }

  .tariffs__price {
    font-size: 18px;
  }

  .tariffs__value {
    padding-top: 6px;
    padding-bottom: 10px;
    padding-left: 15px;
    background-color: #ffffff;
    text-transform: none;
  }
  
  .tariffs__value--yes::before {
    right: 50%;
    margin-right: -10px;
    margin-top: -8px;
  }

  .tariffs__value--no::before {
    right: 50%;
    margin-right: -13px;
    margin-top: -13px;
  }  
}

@media (min-width: $desktop-width) {
  .tariffs { 
    padding-top: 0;
    padding-bottom: 90px;
    
    &::before {     
      top: -100px;
      border: 100px solid transparent;
      border-right: 100vw solid rgb(255, 68, 0);
      border-bottom: 0px solid transparent;
    }
  }

  .tariffs__table {
    width: 940px;  
  }
  
  .tariffs__list {
    width: 235px; 
 
    &:first-child .tariffs__value {
      margin-left: -235px;
    }

    &:first-child .tariffs__value--no::before {
      margin-right: -130px;
    }

    &:first-child .tariffs__value--yes::before {
      margin-right: -130px;
    }
  }
  
  .tariffs__name {
    padding-bottom: 25px;
  }
  
  .tariffs__price {
    font-size: 24px;
  }
  
  .tariffs__value {
    padding-top: 15px;
    padding-bottom: 15px;
    font-size: 18px;
  }

  .tariffs__value--yes::before {
    top: 30px;
  }

  .tariffs__value--no::before {
    top: 30px;
  }    
}
